export default {


    props: {
        paymentFormData: {
            type: Object,
            default() {
                return {}
            }
        },
        disableRadio:{
            type:Boolean,
            default:false
        },
        disableWpay:{
            type:Boolean,
            default:false
        },
        disableApay:{
            type:Boolean,
            default:false
        },
        disabled: {
            type: Boolean,
            default: false
        },

    },
    computed: {
        isWPay() {
            return this.paymentFormData.accountType === 2
        },

    }, 
    methods: {
        validate(fn) {
            this.$refs.paymentform.validate(fn);
        },

    },
    watch: {
        paymentFormData(v) {
            this.$refs.paymentform.resetFields();
        }
    }
}



